import logoImg from "@/assets/newHome/logo/Full_Logo_Vertical_Transparent_Dark.png";
export default {
  name: "newFooter",

  data() {
    return {
      logoImg
    };
  },

  mounted() {
    let self = this; // loadJs('https://cdn.pagesense.io/js/edupassport/81b71de4b3744e65b9740dbe657feac5.js')
    // loadJs('/zoho.js')

    let screenWidth = document.body.clientWidth;
    let screenWidthFloor = Math.floor(screenWidth);

    if (screenWidthFloor <= 768) {
      this.homeArrowRightSize = 30;
    }

    window.onresize = () => {
      if (screenWidthFloor <= 768) {
        this.homeArrowRightSize = 30;
      }
    };

    this.initLoadingStatus = false;
    setTimeout(function () {
      self.initLoadingStatus = false;
    }, 3000);
  },

  unmounted() {
    // console.log('home index unmounted')
    // removeJs('https://cdn.pagesense.io/js/edupassport/81b71de4b3744e65b9740dbe657feac5.js')
    // removeJs('/zoho.js')
    window.onresize = null;
  },

  methods: {
    turnHome() {
      this.$router.push('/');
    },

    turnJobs() {
      this.$router.push('/jobs');
    },

    turnDeals() {
      this.$router.push('/deals');
    },

    turnEvents() {
      this.$router.push('/events');
    },

    turnAboutUs() {
      this.$router.push('/about/us');
    },

    turnSupport() {
      window.open('https://support.edupassport.io', '_blank');
    },

    turnBlogs() {
      window.open('https://blogs.edupassport.io', '_blank');
    }

  }
};